// React
import { useEffect, useRef } from 'react'
import { Routes, Route} from 'react-router-dom'
import './style/style.css'
// Components
import Store from './Components/Store'
import MoreInfoDiv from './Components/MoreInfoDiv'
// Animations
import { gsap } from 'gsap'
// Redux, react-redux and reducers
import { Provider } from 'react-redux'
import { createStore } from 'redux'
import root from './reducers/root'
import LoadScreen from './Components/LoadScreen'


const store = createStore(root);

function App() {
  // In order to animate using GSAP we need access to the element in the DOM. Refs provide a way for us to interact with and store references to DOM nodes in a React component.
  const boxRef = useRef();
  const selector = gsap.utils.selector();

  const loadScreenDelay = 1.3;

  useEffect(() => {
    gsap.to(".loadScreen", {duration: 1, delay: loadScreenDelay, opacity: 0, display: "none"});
    gsap.from(".navigateBetweenSectionCards", {duration: 1.4, delay: loadScreenDelay, opacity: .1, y: "40px"});
    gsap.from(".buyItemHeroSection", {duration: 1.5, delay: loadScreenDelay+.5, opacity: 0, y: "80px"});
  })

  return (
    <div className="App">

      <Provider store={store}>
        <Routes>
            <Route path="/" element={<Store/>} />
            <Route path="detalhes" element={<MoreInfoDiv/>}/>
        </Routes>
      </Provider>
    </div>
  );
}

export default App;













